function onFormSubmit(evt) {
  evt.preventDefault();
  console.log("Ð¤Ð¾Ñ€Ð¼Ð° Ð¾Ñ‚Ð¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð°");
  evt.currentTarget.reset();
  localStorage.removeItem(STORAGE_KEY);
}

// *
//  * - ÐŸÐ¾Ð»ÑƒÑ‡Ð°ÐµÐ¼ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ Ð¿Ð¾Ð»Ñ
//  * - Ð¡Ð¾Ñ…Ñ€Ð°Ð½ÑÐµÐ¼ ÐµÐ³Ð¾ Ð² Ñ…Ñ€Ð°Ð½Ð¸Ð»Ð¸Ñ‰Ðµ
//  * - ÐœÐ¾Ð¶Ð½Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð¸Ñ‚ÑŒ throttle
//  *
function onTextareaInput(evt) {
  const message = evt.target.value;
  localStorage.setItem(STORAGE_KEY, message);
}

// *
//  * - ÐŸÐ¾Ð»ÑƒÑ‡Ð°ÐµÐ¼ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ Ð¸Ð· Ñ…Ñ€Ð°Ð½Ð¸Ð»Ð¸Ñ‰Ð°
//  * - Ð•ÑÐ»Ð¸ Ñ‚Ð°Ð¼ Ñ‡Ñ‚Ð¾-Ñ‚Ð¾ Ð±Ñ‹Ð»Ð¾, Ð¾Ð±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ DOM
//  *
function populateTextarea() {
  const savedMessage = localStorage.getItem(STORAGE_KEY);
  if (savedMessage) {
    console.log(savedMessage);
    refs.textarea.value = savedMessage;
  }
}

// Ð”Ð¾Ð¼Ð¾Ð¹/ ÑÐ´ÐµÐ»Ð°Ñ‚ÑŒ Ñ‚Ð°Ðº Ñ‡Ñ‚Ð¾Ð±Ñ‹ ÑÐ¾Ñ…Ñ€Ð°Ð½ÑÐ»Ð¾ Ð½Ðµ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ Ð½Ð¾ Ð¸ Ð¸Ð¼Ñ, Ð¸ Ð²ÑÐµ Ð² Ð¾Ð´Ð½Ð¾Ð¼ Ð¾Ð±ÑŒÐµÐºÑ‚Ðµ

const formData = {};

refs.form.addEventListener("input", e => {
  // console.log(e.target.name);
  // console.log(e.target.value);

  formData[e.target.name] = e.target.value;
  localStorage.setItem(STORAGE__KEY, JSON.stringify(formData));
  const saveName = localStorage.getItem(STORAGE__KEY);
  const dog = JSON.parse(saveName);
  console.log(dog);
});
